/**
 *   ButtonWrap (button)
 *     Icon (svg)
 *     Text (span)
 */

import styled, { css } from 'styled-components';
import { COLORS } from '../../common/consts/colors';
import { TEXT_CSS } from '../../basic/text';
import type { IButtonProps } from './Button';
import { CSS_BORDER_BOX, CSS_CURSOR_DISABLE, CSS_DISABLE_SELECT, CSS_TRANSITION } from '../../common';

const CSS_PRIMARY = `
  color: ${COLORS.WHITE};
  background-color: ${COLORS.COMMON.BLUE1_COLOR};
  border-color: ${COLORS.COMMON.BLUE1_COLOR};
  &:not([disabled]){
    &:hover {
      background-color: ${COLORS.COMMON.BLUE1_HOVER_COLOR};
      border-color: ${COLORS.COMMON.BLUE1_HOVER_COLOR};
    }
    &:active {
      background-color: ${COLORS.COMMON.BLUE1_ACTIVE_COLOR};
      border-color: ${COLORS.COMMON.BLUE1_ACTIVE_COLOR};
    }
  }
  &:disabled {
    color: ${COLORS.OTHERS.BASIC_WHITE2_COLOR};
    background-color: ${COLORS.COMMON.BLUE1_HOVER_COLOR};
    border-color: ${COLORS.TRANSPARENT};
  }
`;

const CSS_DAVINCI = `
  color: ${COLORS.LIGHT.LEVEL1_BASE_COLOR};
  background-color: ${COLORS.LIGHT.DAVINCI_BLUE_LIGHT};
  border-color: ${COLORS.TRANSPARENT};
  &:not([disabled]){
    &:hover {
      background-color: ${COLORS.LIGHT.DAVINCI_BLUE_LIGHT_BUTTON_HOVER};
    }
    &:active {
      background-color: ${COLORS.LIGHT.DAVINCI_BLUE_LIGHT_BUTTON_ACTIVE};
    }
  }
  &:disabled {
    color: ${COLORS.LIGHT.LEVEL4_BASE_COLOR};
  }
`;



const CSS_NORMAL = `
  color: ${COLORS.LIGHT.LEVEL1_BASE_COLOR};
  background-color: ${COLORS.WHITE};
  border-color: ${COLORS.COMMON.LINE_HARD_COLOR};
  &:not([disabled]){
    &:hover {
      background-color: ${COLORS.OTHERS.DAO_HOVER_BG_COLOR};
    }
    &:active {
      background-color: ${COLORS.COMMON.LINE_HARD_COLOR};
    }
  }
  &:disabled {
    color: ${COLORS.LIGHT.LEVEL4_BASE_COLOR};
  }
`;

const CSS_DANGER = `
  color: ${COLORS.WHITE};
  background-color: ${COLORS.COMMON.DANGER1_COLOR};
  border-color: ${COLORS.COMMON.DANGER1_COLOR};
  &:not([disabled]) {
    &:hover {
      background-color: ${COLORS.COMMON.DANGER1_HOVER_COLOR};
      border-color: ${COLORS.COMMON.DANGER1_HOVER_COLOR};
    }
    &:active {
      background-color: ${COLORS.COMMON.DANGER1_ACTIVE_COLOR};
      border-color: ${COLORS.COMMON.DANGER1_ACTIVE_COLOR};
    }
  }
  &:disabled {
    color: rgba(255, 255, 255, 0.4);
    background-color: ${COLORS.OTHERS.COMMON_DANGER2_COLOR};
    border-color: ${COLORS.TRANSPARENT};
  }
`;

const CSS_GHOST = `
  color: ${COLORS.WHITE};
  border-color: ${COLORS.WHITE};
  background-color: ${COLORS.TRANSPARENT};
  &:not([disabled]) {
    &:hover {
      background-color: rgba(255, 255, 255, 0.12);
      border-color: rgba(255, 255, 255, 0.56);
      color: rgba(255, 255, 255, 1);
    }
    &:active {
      background-color: rgba(17, 31, 44, 0.12);
      border-color: rgba(255, 255, 255, 0.56);
      color: rgba(255, 255, 255, 1);
    }
  }
  &:disabled {
    border-color: rgba(255, 255, 255, 0.5);
    color: rgba(255, 255, 255, 0.5);
  }
`;

const CSS_SUNK = `
  color: ${COLORS.LIGHT.LEVEL1_BASE_COLOR};
  background-color: ${COLORS.TRANSPARENT};
  border-color: ${COLORS.TRANSPARENT};
  &:not([disabled]) {
    &:hover {
      background: ${COLORS.OTHERS.DAO_HOVER_BG_COLOR};
    }
    &:active {
      background: ${COLORS.COMMON.LINE_HARD_COLOR};
    }
  }
  &:disabled {
    color: ${COLORS.LIGHT.LEVEL4_BASE_COLOR};
  }
`;


const CSS_TEXT = `
  color: ${COLORS.COMMON.BLUE1_COLOR};
  background-color: ${COLORS.TRANSPARENT};
  border-color: ${COLORS.TRANSPARENT};
  &:not([disabled]) {
    &:hover {
      color: ${COLORS.COMMON.BLUE1_HOVER_COLOR};
    }
    &:active {
      color: ${COLORS.COMMON.BLUE1_ACTIVE_COLOR};
    }
  }
  &:disabled {
    color: ${COLORS.LIGHT.LEVEL4_BASE_COLOR};
  }
`;


type IButtonWrapProps = Pick<IButtonProps, 'size' | 'disabled'> & {
  btnType: IButtonProps['type'],
  content: 'only-icon' | 'only-text' | 'icon-text'
};


// SplitButton 也需引用到这里
export const CSS_BTN_TYPE = css<{btnType: IButtonProps['type']}>`
  ${({ btnType }) => btnType === 'primary' && CSS_PRIMARY}
  ${({ btnType }) => btnType === 'normal' && CSS_NORMAL}
  ${({ btnType }) => btnType === 'danger' && CSS_DANGER}
  ${({ btnType }) => btnType === 'ghost' && CSS_GHOST}
  ${({ btnType }) => btnType === 'text' && CSS_TEXT}
  ${({ btnType }) => btnType === 'davinci' && CSS_DAVINCI}
  ${({ btnType }) => btnType === 'sunk' && CSS_SUNK}
`;

export const ButtonWrap = styled.button<IButtonWrapProps>`
  ${CSS_BORDER_BOX}
  ${TEXT_CSS.H2}
  ${CSS_DISABLE_SELECT}
  ${CSS_CURSOR_DISABLE}
  ${CSS_TRANSITION('all')}
  border: 1px solid ${COLORS.TRANSPARENT};
  ${CSS_BTN_TYPE}
  border-radius: 4px;
  height: ${({ size }) => size === 'small' ? '28px' : '32px'};
  display: inline-flex;
  align-items: center;
  vertical-align: bottom;
  padding: ${({ size, content }) => content === 'only-icon' ? (size === 'small' ? '0 3px' : '0 4px') : size === 'small' ? '0 8px' : '0 12px'};
  ${({ content }) =>
    // 如果同时有 icon 和 text，则 text 左侧预留 margin
    content === 'icon-text' ? `
      & > :first-child{
        margin-right: 8px;
      }
    ` : null
  }
`;
